// for styles for the text elemetns, h tags, anchor links, etc

// like to set the margin-top to 0
// default has margin top and bottom
// we will set margins later on
h1,
h2,
h3 {
  margin-top: 0;
}

// anchor tags visited
// remove the underline
a,
a:visited,
a:active {
  text-decoration: none;
}
